// cards
// use color cards as template

.color-card {
  border: 0;
  text-align: center;
  @include card-radius;
}
.color-card:hover {
}
.color-card.ca {
  box-shadow: 3px 3px 3px $ash;
  background-color: $smoke;
  border: 10px solid $smoke;
  border-bottom: 40px solid $smoke; //
}
.color-card.ca:hover {
  color: $smoke;
  border-color: $black;
  background-color: $black; }
.color-card.cb {
  height: 80px;
  margin-bottom: 10px;
  padding: 1rem 0;
  border-radius: 2px;
  -webkit-transition-duration: 500ms; /* Safari */
  transition-duration: 500ms;
}
.color-card.cb:hover {

  cursor: pointer;
}
.color-card.cb:active { background-color: $black; }

.color-card.cc {
  height: 50px;
  margin-bottom: 10px;
  padding: 1rem 0;
  border-radius: 2px;
}
.color-card.cc:active { }
