/* partials/_base.scss */
/* begin base stylesheet */
// all your base are belong to us
html { height: 100%; }

* {
  @include typography;
}

body { @include darkmode; }

#header { }

header {
  @include lightmode;
  top: 0;
  right: 0;
  margin: 0 auto;
  position: fixed;
}

#footer { }

footer {
  left: 0;
  bottom: 0;
  margin: 0 auto;
  position: static;
}

nav {
  top: 0;
  right: 0;
  width: 100%;
}

ul {
  display: flex;
  flex-flow: row wrap;
  justify-content: flex-end; }

li { padding: 2rem; }

#{$h} { font-weight: $bold; }

h1 { font-size: $h1; }
h2 { font-size: $h2; }
h3 { font-size: $h3; }
h4 { font-size: $h4; }
h5 { font-size: $h5; }
h6 { font-size: $h6; }

p { font-weight: $regular; }

mark { background-color: $warning; }
strong { font-weight: $bold; }

div {  }

div #{$h} {
  padding: 0 10px;
}

div hr {
  border: 2px solid $coal;
  margin-bottom: 0;
}

.mother {
  @include mother;
}

.row {
  @include m-auto;
}

section {
  align-items: flex-start;
  box-shadow: 4px 4px $coal;
  display: flex;
  height: 1000px;
  justify-content: space-around;
}

.no-corner { @include bl10; }
.big-corner { @include bl10;
  border-bottom-left-radius: 200px;
  border-top-right-radius: 200px;
}
.aaa { border-color: $primary; }
.bbb { border-color: $secondary; }
.ccc { border-color: $tertiary; }
