// partials/_grid.scss
// visual representation of carbon css grid system

.container {
  @include m-auto;
  max-width: 960px;
  display: grid;
  grid-gap: 0rem;
  grid-template-columns: repeat(12, 50px);
  grid-template-rows: 50px;
  padding: 0rem;
  justify-content: space-evenly;
  align-content: space-evenly;
  height: 1000px;
}

.container-3 {
  @include m-auto;
  max-width: 960px;
  display: grid;
  grid-gap: 0rem;
  grid-template-columns: repeat(3, auto);
  grid-template-rows: auto;
  padding: 0rem;
}

.item {
  background-color: $coal;
  border: 1px solid $smoke;
  padding: 0;
  font-size: $h1;
  text-align: center;
  outline: 1px solid red;
outline-offset: 15px;
}

.item-a {
  grid-column: 1 / 9;
  background-color: $ash;

}

.item13 { display: none; }
