.badge {
  width: 40px;
  height: 35px;
  margin: 0 5px;
  padding: 0 3px;
  border: 1px solid $coal;
  border-top-left-radius: 4px;
  border-top-right-radius: 4px;
  border-bottom-right-radius: 4px;
  letter-spacing: .025rem;
}

.badge.success { background-color: $success; }
.badge.danger { background-color: $danger; }
.badge.warning { background-color: $warning; }
.badge.info { background-color: $info; }
.badge.light { background-color: $smoke; color: $coal; }
.badge.dark { background-color: $ash; }
