.note {
  font-weight: $regular;
  border-left: 1rem solid;
  border-top-left-radius: 5px;
  border-bottom-right-radius: 5px;
  border-top-right-radius: 5px;
  height: inherit;
  padding: 1rem 0;
  margin: 0;
  text-align: center;
  box-shadow: 2px 2px $ash-s;
}

// styles

// primary
.note.primary {
  background-color: lighten($primary, 30%);
  border-color: darken($primary, 20%);
  color: $primary;
}
// secondary
.note.secondary {
//  color: $black; //
  background-color: $secondary;
  border-color: darken($secondary, 20%);
}
// info
.note.info {
  background-color: $ash;
  border-color: darken($ash, 20%);
  border-bottom: 0;
  border-right: 0;
  border-bottom-right-radius: 0;
  box-shadow: none;
  height: 80px;
}
// smoke
.note.smoke {
  border: 0px solid;
  border-left: 1rem solid;
  background-color: $smoke;
  border-color: darken($smoke, 20%);
  color: lighten($coal, 40%);
}
// ash
.note.ash {
  border: 0px solid;
  border-left: 1rem solid;
  color: $smoke;
  background-color: $coal--s;
  border-color: $coal;
}
// coal
.note.coal { }
