.z {
  overflow: hidden;
  height: inherit;
  border-bottom: 0px solid $primary;
  border-top: 2px solid $secondary;
  background-color: $smoke;
}
.page-title {
  font-family: $font-family;
  color: $coal;
}

.pre {
  padding-top: 2rem;
  margin-top: 2rem;
  width: 100%;
  background-color: $coal;
}

.post {
  padding-bottom: 2rem;
  margin-bottom: 2rem;
  width: 100%;
  background-color: $grey;
}

.img {border: 2rem solid $coal;}
