// main theme button
button, %button {
  @include darkmode; // theme: smoke on coal
  @include button-type; // uppercase, centered
  @include button-radius; // borders & corners
  @include button-spacetime; // size, position, transition

}

button:hover, button:focus, %button-hover {
  color: $smoke;
  border: 0px solid $coal;
  cursor: pointer; // <-- ??
  background-color: transparent;
}

button:active, %button-active {
  color: $primary;
  @include transparent;
}
button:visited { }
// an other btn //

.button {
  margin-top: 25px; // delete this :-)
  width: 200px;
  @extend %button;
}
.button:hover, .button:focus {
  @extend %button-hover;
}

.button:active {
  @extend %button-active;
}




button.cool {
  color: $coal;
  background-color: $smoke;
  border: 1px solid $coal;
}
button.cool:hover, button.cool:focus {
  color: $smoke;
  background-color: $coal;
  border-color: 1px solid $coal;
}
button.cool:active {
  color: $smoke;
  border-color: $smoke;
  @include transparent;
}


