/* modules/_media.scss */

// MEDIA Shorthand

$breakpoint1: 320px; // small phone viewpoints
$breakpoint2: 480px; // most phones
$breakpoint3: 768px; // tablets
$breakpoint4: 992px; // small desktop viewpoint
$breakpoint5: 1200px; // large & widescreen


// MEDIA mixins

// BREAKPOINT ONE
@mixin media-bp1 {
  .container { width: 85%; padding: 0; }
}
// BREAKPOINT TWO
@mixin media-bp2 {
  .container { width: 80%; }
}








