/* modules/_mixins.scss */
// sass is cool
// GOOGLE fonts
@mixin lato {
  @import url('https://fonts.googleapis.com/css?family=Lato:100,300,400,700,900');
  @import url('https://fonts.googleapis.com/css?family=Lato:100i,300i,400i,700i,900i');
}

// TYPOGRAPHY Mixins
// palindrome
@mixin typography {
  color: $coal;
  font-size: $font-size;
  line-height: $line-height;
  font-weight: $font-weight;
  font-family: $font-family;
}

// base font
@mixin base-font {}
// fixed font
@mixin fixed-font {}


// MOTHER Mixin
@mixin mother {
  background: #7cc6dc linear-gradient(0deg, rgba(124,198,220,1) 0%, rgba(255,81,129,1) 100%) no-repeat;
}


// LINK Mixins
@mixin a {
  weight: $regular;
  transition: color 0.5s;
  text-decoration: none;
}
// HORIZONTAL RULE Mixin
@mixin hr {
  border: 0;
  border-top: 2px solid $coal;
  margin-bottom: 0;
}
// SPACETIME Mixins
@mixin m-auto {
  margin: 0 auto; width: 100%;
}
@mixin button-spacetime {
  width: 100%;
  margin: 0 auto;
  padding: 0.5rem;
  display: inline-block;
  -webkit-transition-duration: 900ms; // Safari only
  // add more transition compatibility
}
// BORDER Mixins
@mixin bl10 {
  border: 0;
  border-left: 10px solid;
}
@mixin border-trans {
  border-color: transparent;
}
// BORDER RADIUS Mixins
@mixin button-radius {
  border: 1px solid $coal;
  border-radius: 5px;
}
@mixin card-radius {
  border-top-left-radius: 9px;
  border-top-right-radius: 0;
  border-bottom-left-radius: 0;
  border-bottom-right-radius: 9px;
}
//BG mixins
@mixin background {
  background-color: #7cc6dc;
}
@mixin bg-gradient {}
// Opacity & Transparency Mixins
@mixin opacity {
  opacity: 1;
}
// currently switched off
@mixin transparent {
  background-color: transparent;
  border-color: transparent;
}
// TRANSITION mixins
@mixin color-transit {
  transition: color 0.3s;
}
@mixin bg-transit {
  -webkit-transition-duration: 900ms;
  /* Safari */
}
// COLOR Mixins
// DARK MODE
@mixin darkmode {
  color: $smoke;
  background-color: $coal;
}
// LIGHT MODE
@mixin lightmode {
  color: $coal;
  background-color: $smoke;
}

// BUTTON Mixins
@mixin button-type {
  font-weight: $regular;
  text-align: center;
  text-transform: uppercase;
}
