/* modules/_shorthand.scss */
// Sassy variable shorthand for carbon.css
// github.com/sciencebymail/carbon
// <3

// Colors
$smoke: #F5F5F5;
$ash: #B6B6B6;
$coal: #343434;
$primary: #4BB1CF;
$secondary: #FF1053;
$tertiary: #B4DC7F;
$warning: #EED000;
$danger: $secondary;
$success: $tertiary;
$info: #94C5CC;

// Font Weights
$thin: 100;
$light: 300;
$regular: 400;
$bold: 700;
$black: 900;

// Base Font
$font: 'Lato';
$color: #343434;
$font-size: 1.4rem;
$line-height: 1.15; //
$font-weight: $regular;
$font-family: $font, Helvetica, sans-serif;

// Fixed Font
$fixed-font-size: 1rem;
$fixed-fot-family: monospace;
$fixed-line-height: $line-height;

// Headings

$h: 'h1, h2, h3, h4, h5, h6';

$h1: 4.0rem;
$h2: 3.6rem;
$h3: 3.0rem;
$h4: 2.4rem;
$h5: 1.8rem;
$h6: 1.6rem;

// LINK Shorthand

$a: 'a, a:active, a:focus, a:hover, a:visited';
$hover: 'a:hover, a:focus';
$nav-a: 'nav a, nav a:active, nav a:focus, nav a:hover, nav a:visited';
