/* modules/_colors.scss */

// 1.a $coal & $smoke, $gray
// 1.b $primary, $secondary, & $tertiary colors
// 1.c Notes, Alerts, Warnings

// begin

// Section 1 Color Stretch
// ------------------------------------------------- //

$stretch: 20%;

// Section 1.a $coal & $smoke, $gray
// ------------------------------------------------- //

// WHITE - SMOKE
$fff: #FFF;
$smoke: #F5F5F5;

// SMOKE Stretch
$smoke-s: darken($smoke, $stretch);

// GRAY && GREY - ASH
$gray: #B6B6B6;
$grey: #B6B6B6;
$ash: #B6B6B6;

// Ash Stretch
$ash--s: lighten($gray, $stretch);
$ash-s: darken($gray, $stretch);

// BLACK - COAL
$color: #343434;
$zzz: #000;
$coal: #343434;

// Coal Stretch
$coal--s: lighten($coal, $stretch);

// Section 1.b Primary, secondary, & tertiary colors //
// ------------------------------------------------- //

// PRIMARY
$primary: #4BB1CF;
// Primary stretch
$primary--s: lighten($primary, $stretch);
$primary-s: darken($primary, $stretch);

// SECONDARY
$secondary: #FF1053;
// Secondary stretch
$secondary--s: lighten($secondary, $stretch);
$secondary-s: darken($secondary, $stretch);

// TERTIARY
$tertiary: #B4DC7F;
// Tertiary stretch
$tertiary--s: lighten($tertiary, $stretch);
$tertiary-s: darken($tertiary, $stretch);

// Section 1.c - Notes, Alerts, Warnings
// ------------------------------------------------- //

// SUCCESS
$success: #B4DC7F; // via Terri, needs to be darker
// DANGER WILL ROBINSON
$danger: #FF1053; // via Terri
// WARNINGS
$warning: #EED000; // not perfect yet
// $warning: #FFBE0B; // via Terri

// INFO
$info: #94c5cc; // ? not currently using
