// alerts

.alert {
  color: $smoke;
  font-weight: $regular;
  text-transform: uppercase;
  border-radius: 5px;
  height: inherit;
  padding: 1rem 0;
  margin: 0;
  text-align: center;
  box-shadow: 2px 2px 2px darken($smoke, 20%);
}

.alert.success {
  background-color: $success;
}

.alert.danger {
  background-color: $danger;
}

.alert.warning {
  background-color: $warning;
}
